<template>
   <div class="container">
      <div class="row">
        <section class="col-12">
          <h1>Professors</h1>
          <table class="table table-hover">
              <tr class="header_table">
                  <th>Id</th>
                  <th>Name</th>
                  <th>Email</th>
                  <th>Tag id</th>
                  <th>Edit</th>
                  <th>Delete</th>
              </tr>
              <tr v-for="professor in professors">
                  <td>{{professor.id}}</td>
                  <td>{{professor.name}}</td>
                  <td>{{professor.email}}</td>
                  <td>{{professor.tagId}}</td>
                  <td><router-link class="nav-button" :to="'/professor/edit/' + professor.id">Edit professor</router-link></td>
                  <td><button class="delete" v-on:click=deleteProfessor(professor.id)>&#x274C;</button></td>
              </tr>
          </table>
        </section>
        <div class="col-12 navigation_buttons">
          <ul>
              <li><router-link class="nav-button" to="/professor/add">Add Subject</router-link></li>
          </ul>
       </div>
      </div>
    </div>
</template>

<script>

import axios from 'axios';

export default {
  name: 'adminProfessor',
  data () {
    return {
      professors: []
    }
  },
  methods: {
      getProfessors(){
          axios.get(this.$store.getters.getUrl + 'api/professors')
        .then(function (response) {
            this.professors = response.data;
            console.log(response.data);
        }.bind(this))
        .catch((error)=>{
          this.$router.push('/login'); 
        });
      },
      deleteProfessor(id){
         axios.delete(this.$store.getters.getUrl + 'api/professors/'+ id)
         .then(function (response) {
            alert(response.data['deleted']);
            this.getProfessors();
        }.bind(this))
        .catch((error)=>{
          this.$router.push('/login'); 
        });
      }
  },
  created: function () {
    if(!localStorage.getItem('token')){
      this.$router.push('/login'); 
    }
    this.getProfessors();
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



